@import "partials/common/imports";

$text-grey: #666662;
$lighther-gray: #f9f9f9;

.page {
  overflow: hidden;
}

// Elemental header
.page__header {
  background: $white;
  min-height: 66px;
  padding: 19px 0 0;

  @include breakpoint($mobile-only) {
    border: none;
  }

  @include breakpoint($desktop) {
    padding-top: 12px;
  }
}

.page__header__inner {
  max-width: 1434px;
  margin: auto;
}

// Logo
.elemental__logo {
  float: left;
  margin: 5px 0 0 31px;

  @include breakpoint($tablet) {
    margin-left: 37px;
  }

  @include breakpoint($desktop) {
    margin-left: 20px;
  }

  @include breakpoint($desktop-wide) {
    margin-left: 2%;
  }
}

.elemental__logo--desktop {
  display: none;

  @include breakpoint($desktop) {
    display: block;
    max-width: 118px;
  }
}

.elemental__logo--mobile {
  max-width: 43px;

  @include breakpoint($desktop) {
    display: none;
  }
}

.elemental__header--menu-action {
  float: right;
  margin-top: 4px;

  @include breakpoint($desktop) {
    display: none;
  }
}

.elemental__header--open,
.elemental__header--close {
  color: #40434c;
  cursor: pointer;
  display: inline-block;
  height: 21px;
  float: none;
  font-size: 20px;
  line-height: 21px;
  margin-left: 22px;
  vertical-align: middle;

  .header &:before {
    color: $deep-charcoal;
    font-size: 20px;
  }

  @include breakpoint($desktop) {
    display: none;
  }
}

.elemental__header--close {
  display: none;
}

.js-nav {
  .elemental__header--close {
    display: inline-block;
  }

  .elemental__header--open {
    display: none;
  }
}

// MENU
.header__menu {
  background: $white;
  padding-bottom: 68px;
  position: absolute;
  top: 66px;
  width: 100%;

  @include breakpoint($tablet) {
    display: none !important;

    .js-nav & {
      display: block !important;
    }
  }

  @include breakpoint($desktop) {
    padding-bottom: 0;
    position: static;
  }

  @include breakpoint($desktop) {
    display: block !important;
  }

  .header-left {
    margin-bottom: 12px;
    padding: 68px 29px 36px;

    @include breakpoint($tablet) {
      width: auto;
    }

    @include breakpoint($desktop) {
      margin: 10px 0 0 40px;
      padding: 0;
      position: static;
    }

    @include breakpoint($desktop-wide) {
      margin-left: 5.5%;
    }
  }
}

.elemental__header__menu {
  font-size: 14px;
  padding: 0;

  @include breakpoint($desktop) {
    display: flex;
    margin-bottom: 0;
  }

  li {
    font-size: 19px;
    margin-bottom: 12px;

    @include breakpoint($desktop) {
      font-size: 14px;
      margin-left: 30px;
      margin-bottom: 0;
    }

    @include breakpoint($desktop-wide) {
      margin-left: 47px;
    }

    &:first-child {
      margin-left: 0;
    }
  }

  a {
    @include grotesk-light;
    color: $vibrant-blue;
    display: block;

    @include breakpoint($desktop) {
      @include grotesk-regular;
      border-bottom: 4px solid transparent;
      color: $deep-charcoal;
      padding-bottom: 22px;
    }

    &:hover {
      text-decoration: none;
    }
  }

  .active a {
    color: $deep-charcoal;
    text-decoration: none;

    @include breakpoint($desktop) {
      @include grotesk-bold;
      border-bottom-color: $vibrant-blue;
    }
  }
}

// BUTTONS
.region--header_btns {
  display: inline-block;
  vertical-align: middle;
}

.elemental__header__buttons {
  font-size: 0;
  float: right;
  margin: 0 18px 0 0;

  @include breakpoint($tablet) {
    margin-right: 46px;
  }

  @include breakpoint($desktop) {
    margin-right: 20px;
    margin-top: 6px;
  }

  @include breakpoint($mobile-tablet-only) {
    width: 50%;
  }

  a {
    @include grotesk-bold;
    font-size: 10px;
    letter-spacing: -0.4px;
    line-height: 22px;
    padding: 4px 8px;

    @include breakpoint($desktop) {
      margin: 0 0 0 16px;
    }

    @include breakpoint($desktop-wide) {
      padding: 12px 22px;
      font-size: 12px;
      letter-spacing: -0.2px;
      line-height: normal;
    }
  }

  @include breakpoint($desktop-wide) {
    margin-right: 4.3%;
    margin-top: 0;
  }

  .elemental__header--chat,
  .elemental__header--scope {
    display: none;

    @include breakpoint($desktop) {
      display: inline-block;
    }
  }

  .elemental__header--chat {
    padding: 0;
  }
}

.elemental__header__buttons--mobile {
  font-size: 12px;
  padding: 0 20px;

  @include breakpoint($desktop) {
    display: none;
  }

  a {
    @include grotesk-bold;
  }
}

.elemental__header--chat {
  line-height: 30px;
  float: right;
  text-transform: uppercase;

  @include breakpoint($desktop) {
    font-size: 12px;
    padding: 0;
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    float: none;
  }

  &:before {
    font-size: 18px;
    float: none;
    display: inline-block;
    margin-right: 14px;
    vertical-align: middle;
  }
}

.elemental__header--demo {
  display: none;
}

.elemental__header--demo--tablet {
  @include grotesk-bold;
  display: inline-block;
  vertical-align: middle;
  float: right;

  @include breakpoint($desktop-wide) {
    display: none;
  }
}

.elemental__header--demo--desktop {
  @include breakpoint($desktop-wide) {
    display: inline-block;
  }
}

// Elemental page content

.elemental__content {
  @include grotesk-light;
  padding-top: 66px;
}

.elemental__section {
  position: relative;

  &.container {
    margin-bottom: 0;

    @include breakpoint($desktop) {
      margin: auto;
      max-width: 1434px;
      width: 100%;
    }

    @include breakpoint($desktop-wide) {
      width: 100%;
    }
  }
}

.elemental--dark-gradient {
  background: rgb(40, 97, 126); /* Old browsers */
  background: -moz-linear-gradient(left, rgba(40, 97, 126, 1) 0%, rgba(52, 62, 72, 1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, rgba(40, 97, 126, 1) 0%, rgba(52, 62, 72, 1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, rgba(40, 97, 126, 1) 0%, rgba(52, 62, 72, 1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.elemental__title {
  margin-bottom: 30px;
  font-size: 24px;
  line-height: 34px;

  @include breakpoint($tablet) {
    font-size: 30px;
    line-height: 43px;
  }

  @include breakpoint($desktop) {
    font-size: 36px;
  }
}

// Watch link
.elemental--watch {
  color: $white;
  display: inline-flex;
  padding-top: 6px;
  padding-bottom: 6px;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $white;
  }
}

.elemental--watch__icon {
  width: 87px;
  height: 47px;
  position: relative;

  &:before {
    background: $white;
    display: block;
    content: "";
    height: 100%;
    left: 0;
    opacity: .8;
    position: absolute;
    top: 0;
    z-index: 1;
    width: 100%;
  }

  &:after {
    content: '';
    border-style: solid;
    border-width: 8px 0 8px 12px;
    border-color: transparent transparent transparent $vibrant-blue;
    height: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 0;
    z-index: 2;
  }

  &:hover {
    text-decoration: none;
  }
}

.elemental--watch__text {
  @include grotesk-regular;
  font-size: 15px;
  float: none;
  line-height: 47px;
  margin-left: 18px;
  text-transform: initial;

  @include breakpoint($desktop) {
    @include grotesk-light;
    font-size: 13px;
  }
}

.elemental--watch--blue {
  color: $vibrant-blue;

  &:hover,
  &:focus {
    color: $vibrant-blue;
  }

  .elemental--watch__icon {
    &:before {
      background: $vibrant-blue;
    }

    &:after {
      border-color: transparent transparent transparent $white;
    }
  }
}

// Series Deck
.elemental__series--top {
  position: relative;
}

.elemental__series__background {
  max-height: 700px;

  .vjs-control-bar {
    display: none !important;
  }
}

.elemental__series__video {
  display: none;
}

.elemental__series__video__inner {
  padding-top: 56.25%;
  position: relative;
}

.elemental__series__content {
  color: $white;
  padding-left: 15%;
  padding-right: 15%;
  text-align: center;
  width: 100%;

  @include breakpoint($mobile-only) {
    background: $deep-charcoal;
  }
  @include breakpoint($tablet) {
    left: 0;
    position: absolute;
    top: 52%;
    transform: translateY(-50%);
    padding-left: 13%;
    padding-right: 13%;
  }

  @include breakpoint($desktop) {
    top: 50%;
  }

  h1 {
    font-size: 24px;
    margin: 0 auto 12px;
    padding-top: 40px;

    @include breakpoint($tablet) {
      font-size: 60px;
      padding-top: 20px;
      margin-bottom: 30px;
    }

    @include breakpoint($desktop) {
      margin-bottom: 16px;
    }
  }

  p {
    @include grotesk-light;
    color: $white;
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;

    @include breakpoint($tablet) {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 26px;
    }

    @include breakpoint($desktop) {
      line-height: 26px;
      margin-bottom: 40px;
    }
  }
}

.elemental__series__video__icon {
  left: 50%;
  cursor: pointer;
  font-size: 0;

  @include breakpoint($mobile-only) {
    position: absolute;
    top: 28.25vw;
    transform: translate(-50%, -50%);
  }

  &:before {
    font-size: 60px;

    @include breakpoint($tablet) {
      font-size: 90px;
    }
  }
}

// Series Deck Bottom
.elemental__series--bottom {
  align-items: center;
  color: $white;
  justify-content: space-around;
  padding: 42px 0 50px;
  position: relative;

  @include breakpoint($tablet) {
    padding-top: 84px;
    padding-bottom: 32px;
  }

  @include breakpoint($desktop) {
    padding-bottom: 95px;
  }

  .item {
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    margin-bottom: 38px;

    @include breakpoint($tablet) {
      margin-bottom: 54px;
    }

    @include breakpoint($desktop) {
      font-size: 18px;
      line-height: 24px;
      margin: 0;
    }

    img {
      margin-bottom: 12px;
    }
  }
}

.elemental__series--bottom__title {
  @extend .elemental__title;
  text-align: center;
  padding: 0 15%;
  margin-bottom: 72px;

  @include breakpoint($tablet) {
    margin-bottom: 60px;
  }
}

.elemental__series--bottom--inner {
  display: flex;
  flex-wrap: wrap;

  img {
    display: inline-block;
  }
}

.elemental__icon-scroll {
  cursor: pointer;
  display: none;
  left: 50%;
  position: absolute;
  top: 0;
  transform: translate(-50%, -40%);

  @include breakpoint($desktop) {
    display: block;
  }
}

// Display Deck
.elemental__display {
  padding-bottom: 23px;
  padding-top: 40px;

  @include breakpoint($tablet) {
    padding-bottom: 42px;
    padding-top: 92px;

    &.container {
      padding-left: 56px;
    }
  }

  @include breakpoint($desktop) {
    padding-bottom: 136px;
    padding-top: 160px;

    &.container {
      padding-left: 15px;
    }
  }
}

.elemental__display--left {
  text-align: center;
  padding: 0 30px;
  margin-bottom: 22px;

  @include breakpoint($tablet) {
    text-align: left;
  }

  .elemental__title {
    margin-bottom: 20px;
    padding: 0 20px;

    @include breakpoint($tablet) {
      line-height: 37px;
      margin-bottom: 20px;
      padding: 0;
    }

    @include breakpoint($desktop) {
      line-height: 43px;
      margin-bottom: 35px;
    }
  }
}

// Tabs
.elemental__display__tabs {
  display: none;

  @include breakpoint($tablet) {
    display: block;
    margin-top: 40px;
  }

  @include breakpoint($desktop) {
    margin-top: 34px;
  }
}

.elemental__display__tabs--mobile {
  padding-left: 59px;
  padding-top: 26px;

  @include breakpoint($tablet) {
    display: none;
  }
}

.elemental__display__tab {
  @include grotesk-regular;
  align-items: flex-start;
  display: flex;
  margin-bottom: 20px;
  min-height: 36px;
  padding-left: 0;
  padding-top: 5px;
  position: relative;
  text-align: left;
}

.elemental__display__tab__title {
  font-size: 18px;
  color: $text-grey;
  cursor: pointer;
  line-height: 26px;
  display: block;

  .elemental__display__tab.active & {
    color: $vibrant-blue;
  }
}

.elemental__display__tab--icon {
  height: 36px;
  margin-right: 20px;
  position: relative;
  top: -4px;
  width: 36px;

  &:before {
    background: $vibrant-blue;
    border-radius: 50%;
    content: '';
    display: block;
    height: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 0;

    .elemental__display__tab.active & {
      transition: all .2s;
      height: 36px;
      width: 36px;
    }
  }

  &:after {
    background: center no-repeat;
    content: '';
    display: block;
    height: 36px;
    left: 0;
    position: absolute;
    top: 0;
    width: 36px;
    z-index: 1;
  }
}

.elemental__display__tab-1 {
  .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-1.png');
  }

  &.active .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-1-hover.png');
  }
}

.elemental__display__tab-2 {
  .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-2.png');
  }

  &.active .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-2-hover.png');
  }
}

.elemental__display__tab-3 {
  .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-3.png');
  }

  &.active .elemental__display__tab--icon:after {
    background-image: url('../images/elemental/icon-animation-3-hover.png');
  }
}

.elemental__gesture__videos {
  padding: 0;
  position: relative;

  @include breakpoint($mobile-only) {
    float: right;
    width: 93%;
  }

  @include breakpoint($tablet) {
    margin-top: 160px;
  }

  @include breakpoint($desktop) {
    margin-top: 42px;
    float: right;
    width: 57.2%;
  }

  img {
    width: 100%;
    max-width: 100%;
    height: auto;
  }
}

.elemental__gestures {
  left: 4.2%;
  position: absolute;
  top: 8.1%;
  width: 82.3%;

  .video-js {
    width: 100%;
    height: auto;

    video {
      position: static;
      max-width: 100%;
      height: auto;
      width: 100%;
    }
  }

  [data-tab-id] {
    display: none;
  }
}

// Decks  channels and resolution
.elemental__channels,
.elemental__resolution {
  background-color: #f7f7f7;

  @include breakpoint($mobile-only) {
    text-align: center;
  }
}

// Deck channels
.elemental__channel-input {
  text-align: left;

  @include breakpoint($mobile-only) {
    padding-top: 24px;
  }

  @include breakpoint($tablet) {
    white-space: nowrap;
  }

  p {
    color: $dark-gray;
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 25px;
  }

  & > p {
    margin-bottom: 10px;

    @include breakpoint($mobile-only) {
      font-size: 11px;
    }

    @include breakpoint($tablet) {
      margin-bottom: 15px;
    }
  }
}

.elemental__channel-input__inner {
  background: url('../images/elemental/deck3-inputs.png') no-repeat left 5px;
  min-height: 135px;
  padding-left: 205px;
  margin-bottom: 41px;

  @include breakpoint($mobile-only) {
    background-size: 160px;
    min-height: 120px;
    padding-left: 170px;

    p {
      font-size: 11px;
    }
  }
}

.elemental__channels__text {
  @include breakpoint($mobile-only) {
    padding: 0 30px 50px 30px;
  }

  @include breakpoint($tablet) {
    padding-bottom: 100px;
    padding-top: 100px;
  }
}

// Deck resolution
.elemental__resolution {
  z-index: 2;
  margin: auto;
  max-width: 1434px;
}

.elemental__resolution__img {
  @include breakpoint($mobile-only) {
    margin-bottom: -50px;
  }

  @include breakpoint($tablet) {
    position: absolute;
    bottom: -50px;
  }

  @include breakpoint($desktop) {
    position: relative;
    bottom: -100px;
  }
}

.elemental__resolution__text {
  @include breakpoint($mobile-only) {
    padding: 44px 30px 0 30px;
  }

  @include breakpoint($tablet) {
    padding-bottom: 60px;
    padding-top: 100px;
  }
}

// Sliders
.elemental__slider {
  .slick-slider {
    margin-bottom: 0;
    max-width: none;
    padding: 0;
  }

  .item img {
    width: 100%;
    height: auto;
  }

  h2 {
    @include grotesk-light;
    color: $white;
    font-size: 24px;
    line-height: 31px;
    margin-bottom: 26px;

    @include breakpoint($tablet) {
      font-size: 29px;
      line-height: 36px;
      margin-bottom: 26px;
    }

    @include breakpoint($desktop) {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 36px;
    }
  }

  h4 {
    @include grotesk-regular;
    color: $white;
    font-size: 14px;
    margin-bottom: 4px;

    @include breakpoint($tablet) {
      font-size: 16px;
    }

    @include breakpoint($desktop) {
      font-size: 18px;
      margin-bottom: 10px;
    }
  }

  .elemental__slider--role {
    @include grotesk-light;
    margin-top: 2px;
    margin-bottom: 35px;

    @include breakpoint($desktop) {
      margin-bottom: 45px;
    }
  }

  // Controls
  .slick-arrow {
    height: 50px;
    display: none !important;
    background: $vibrant-blue;
    border-radius: 50%;
    line-height: 50px;
    width: 50px;
    top: 50%;

    @include breakpoint($tablet) {
      display: block !important;
    }

    &:before {
      color: $white;
      display: block;
      height: 50px;
      line-height: 50px;
      width: 50px;
    }
  }

  .slick-prev {
    left: 30px;
  }

  .slick-next {
    right: 30px;
  }
}

.elemental__slider--caption {
  bottom: auto;
  height: auto;
  left: 20%;
  min-height: 0;
  padding: 0;
  position: absolute;
  right: 20%;
  top: 50%;
  transform: translateY(-50%);
  width: auto;
  text-align: center;

  @include breakpoint($mobile-only) {
    left: 10%;
    right: 10%;
    position: absolute;
  }
}

// Quotes slider
.elemental__slider--testimonials {
  .slick-arrow {
    background: $white;

    &:before {
      color: $vibrant-blue;
    }
  }

  .item {
    min-height: 500px;

    .embedded-entity img,
    & > img {
      height: auto;
      left: 50%;
      min-height: 100%;
      top: 0;
      transform: translateX(-50%);
      position: absolute;
      min-width: 100%;
      width: auto;
      max-width: none;
      max-height: 100%;

      @include breakpoint($tablet) {
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        max-width: 100%;
        max-height: none;
      }
    }
  }

  .btn {
    &:focus,
    &:hover {
      color: $white;
    }
  }

  // Dots
  .slick-dots {
    bottom: 24px;
    left: 50%;
    margin: 0;
    transform: translateX(-50%);
    width: auto;

    @include breakpoint($tablet) {
      bottom: 30px;
    }

    @include breakpoint($desktop) {
      bottom: 8px;
    }

    li {
      width: auto;
      height: auto;
      margin: 0 3px;
    }

    button {
      background: $white;
      border-radius: 0;
      border: none;
      height: 2px;
      width: 30px;
    }

    .slick-active button {
      background: $vibrant-blue;
    }
  }

  .slick-track {
    overflow: hidden;
  }
}

// Products slider
.elemental__slider--products {
  .item {
    background: #f4f4f4;

    img {
      display: block;
      height: auto;
      margin: auto;
      width: 615px;
    }

    .parent {
      padding: 35px 32px 89px;

      @include breakpoint($tablet) {
        padding: 56px 147px 112px;
      }

      @include breakpoint($desktop) {
        padding: 62px 32px 100px;
      }
    }

    .elemental__slider--products__caption,
    .elemental__slider--products__thumb {
      display: none;
    }
  }

  .slick-dots {
    margin: 0;
    height: auto;
    display: flex;
    overflow: auto;
    position: static;
    text-align: left;
    padding: 0;
    flex-wrap: wrap;
    width: 100%;

    @include breakpoint($tablet) {
      flex-wrap: nowrap;
    }

    li {
      background-color: transparent;
      border-radius: 0;
      border: 0;
      height: auto;
      overflow: hidden;
      margin: 0 !important;
      position: relative;
      width: 33.3%;

      @include breakpoint($tablet) {
        width: 16.66666667%;
      }
    }

    img {
      height: auto;
      display: block;
      position: absolute;
      left: 50%;
      top: 0;
      max-height: 100%;
      max-width: none;
      min-height: 100%;
      width: auto;
      transform: translateX(-50%);
    }

    .slick-active {
      .slider-indicator--inner:after {
        display: block;
        height: 100%;
        background: $vibrant-blue;
        content: '';
        opacity: .95;
        left: 0;
        top: 0;
        position: absolute;
        width: 100%;
        z-index: 1;
      }

      .elemental__slider--products__caption {
        display: block;
      }
    }
  }

  .elemental__slider--products__caption {
    color: $white;
    display: none;
    left: 0;
    top: 50%;
    text-align: center;
    transform: translateY(-50%);
    padding: 0 5px;
    position: absolute;
    text-indent: 0;
    white-space: initial;
    width: 100%;
    z-index: 2;
    font-size: 12px;

    @include breakpoint($desktop) {
      font-size: 16px;
    }
  }

  .slider-indicator--inner {
    height: 0;
    padding-top: 56%;
    width: 100%;
  }

  // Zoom buttons
  .buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: -73px;
    margin-bottom: -40px;

    @include breakpoint($tablet) {
      top: -68px;
    }

    @include breakpoint($desktop) {
      top: -85px;
    }

    button {
      @include grotesk-light;
      border-radius: 50%;
      border: none;
      width: 36px;
      height: 36px;
      overflow: hidden;
      background: $vibrant-blue;
      color: $white;
      font-size: 21px;
      line-height: 36px;
      text-align: center;
      padding: 0;
      margin: 0 10px;

      @include breakpoint($tablet) {
        height: 40px;
        line-height: 40px;
        width: 40px;
      }

      &:focus {
        outline: none;
      }
    }

    input[type=range] {
      -webkit-appearance: none;
      width: 230px;

      @include breakpoint($tablet) {
        width: 250px;
      }
    }

    // Track
    input[type=range]::-webkit-slider-runnable-track {
      height: 1px;
      background: $vibrant-blue;
    }

    input[type=range]::-moz-range-track {
      height: 1px;
      background: $vibrant-blue;
    }

    input[type="range"]::-ms-fill-lower,
    input[type="range"]::-ms-fill-upper {
      background: $vibrant-blue;
    }
    input[type="range"]::-ms-track {
      background: transparent;
      border-color: transparent;
      border-width: 15px 0;
      height: 1px;
      margin-top: 10px;
    }

    // Thumb
    input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
      background: $vibrant-blue;
      border: none;
      border-radius: 50%;
      cursor: pointer;
      height: 22px;
      margin-top: -10px;
      width: 22px;

      @include breakpoint($tablet) {
        height: 25px;
        margin-top: -12px;
        width: 25px;
      }
    }

    input[type=range]::-moz-range-thumb {
      background: $vibrant-blue;
      border: none;
      border-radius: 50%;
      height: 22px;
      cursor: pointer;
      margin-top: -10px;
      width: 22px;

      @include breakpoint($tablet) {
        height: 25px;
        margin-top: -12px;
        width: 25px;
      }
    }

    input[type="range"]::-ms-thumb {
      height: 22px;
      border-radius: 50%;
      background: $vibrant-blue;
      cursor: pointer;
      margin-top: 2px;
      width: 22px;

      @include breakpoint($tablet) {
        height: 25px;
        margin-top: 2px;
        width: 25px;
      }
    }

    input[type=range]:focus {
      outline: none
    }
  }
}

// Specs Deck
.elemental__specs {
  padding-top: 157px;
  padding-bottom: 58px;

  @include breakpoint($tablet) {
    padding-top: 130px;
    padding-bottom: 90px;
  }

  @include breakpoint($desktop) {
    padding-top: 210px;
    padding-bottom: 75px;
  }

  img {
    display: inline-block;
  }

  .elemental__title {
    text-align: center;
    margin-bottom: 45px;

    @include breakpoint($tablet) {
      margin-bottom: 30px;
    }

    @include breakpoint($desktop) {
      margin-bottom: 26px;
      text-align: left;
    }
  }

  .item {
    margin-bottom: 20px;

    &,
    & p {
      font-size: 14px;
      line-height: 21px;
    }
  }
}

.elemental__specs--left {
  margin-bottom: 52px;
  text-align: center;

  @include breakpoint($tablet) {
    margin-bottom: 56px;
  }

  @include breakpoint($desktop) {
    max-width: none;
    text-align: left;
  }

  & > * {

    @include breakpoint($tablet) {
      max-width: 374px;
      margin: auto;
    }

    @include breakpoint($desktop) {
      max-width: none;
    }
  }

  .item {
    &:first-child {
      display: none;
      text-align: left;

      @include breakpoint($tablet) {
        display: block;
      }
    }

    br {
      @include breakpoint($tablet) {
        display: none;
      }

      @include breakpoint($desktop) {
        display: block;
      }
    }
  }
}

.elemental__specs--image {
  height: auto;
  margin-bottom: 18px;
  max-width: 324px;
  width: 100%;

  @include breakpoint($tablet) {
    max-width: 100%;
    margin-bottom: 8px;
  }

  @include breakpoint($desktop) {
    margin-bottom: 25px;
    width: auto;
  }
}

.elemental--download {
  @include grotesk-regular;
  align-items: center;
  display: inline-flex;
  font-size: 14px;
  line-height: 17px;
  margin-top: 10px;

  @include breakpoint($tablet) {
    max-width: none;
    margin-top: 0;
  }

  @include breakpoint($desktop) {
    display: flex;
  }

  &:hover {
    color: $vibrant-blue;
  }

  img {
    height: auto;
    margin-right: 15px;
    margin-top: 2px;
    max-width: 28px;
  }
}

.elemental__specs--content {
  padding: 0 37px;

  @include breakpoint($tablet) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.elemental__specs--footer-text {
  line-height: 18px;
  font-size: 10px;

  @include breakpoint($tablet) {
    font-size: 11px;
  }
}

// Specs Deck
.elemental__deck-8 {
  padding-top: 50px;

  &, p {
    color: $white;
    font-size: 14px;
    line-height: 21px;
  }

  .item {
    margin-bottom: 20px;
  }

  @include breakpoint($mobile-only) {
    background-color: #28617e;
    text-align: center;

    img {
      margin: auto;
    }
  }

  @include breakpoint($tablet) {
    background: #28617e url('../images/elemental/deck8-mobile.png') no-repeat -100px 50px;
    background-size: 55%;
    padding-bottom: 70px;
    padding-top: 100px;

    .elemental__title {
      margin-bottom: 60px;
    }
  }

  @include breakpoint($desktop-wide) {
    background: #28617e url('../images/elemental/deck8-bg.png') no-repeat left bottom;
    background-size: 42%;
  }
}

// Deck Quote
.elemental__quote {
  text-align: center;
  padding-bottom: 72px;
  padding-top: 72px;

  img {
    display: inline-block;
  }

  .btn {
    line-height: 1;
    margin-top: 22px;
    padding: 14px 25px;
  }
}

.elemental__quote__links {
  display: inline-flex;
  flex-direction: column;

  a {
    font-size: 19px;
    margin-bottom: 20px;

    &:before {
      display: inline-block;
      font-size: 22px;
      margin-right: 15px;
      vertical-align: middle;
    }
  }
}

.elemental__quote__btns {
  &:before {
    background: $deep-charcoal;
    content: '';
    display: block;
    height: 1px;
    max-width: 100%;
    margin: auto;
    width: 265px;
  }
}

// Modals
.ui-dialog {
  background: transparent;
  border-radius: 0;
  border: none;
  display: flex;
  height: 100% !important;
  padding-top:  40px;
  padding-bottom: 40px;
  flex-direction: column;
  justify-content: center;
  left: 0 !important;
  top: 0 !important;
  position: fixed;
  width: 100% !important;
  z-index: 1045;

  .ui-dialog-content {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    overflow: hidden;
    width: 100% !important;
  }
}

.ui-widget-overlay {
  background: #0b0b0b;
  opacity: 0.8;
}

.ui-draggable .ui-dialog-titlebar {
  width: 100%;
  max-width: 900px;
  border-radius: 0;
  border: none;
  margin-right: auto;
  margin-left: auto;
}

.video-js {
  width: 100%;
  height: auto;

  .vjs-tech {
    position: static;
  }
}

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default {
  background: transparent;
  border: none;
  color: $vibrant-blue;
  height: 22px;
  float: right;
  font-size: 20px;
  position: static;
  width: 22px;
}
